import React from 'react'
import CommentCard from './CommentCard'

function BlogCard({blog:{blogPost, blogTitle,id,image,comments}}) {
  
    return (
        <div>
            <h1>{blogTitle}</h1>
            <img className="blogImage" src={image} />
            <br />
            <p>{blogPost}</p>
            <CommentCard 
            comments={comments}
            id={id}
            />
        </div>
    )
}

export default BlogCard
