import React from 'react'

function CommentForm({commentSubmitHandler,commentChangeHandler,FormData:{name,comment}}) {
 


    return (
        <form onSubmit={commentSubmitHandler}>
            <label> name</label>
                <input onChange={commentChangeHandler}name="name" type="text" placeholder="Name" value={name} ></input>
            <label>Comment </label>
                <input onChange={commentChangeHandler} name="comment"  placeholder="Comment"  value={comment}     type="text"></input>
            <button type="submit" value="Submit"   >Submit</button>
        </form>
    )
}

export default CommentForm


